<template>
  <div class="showContent detailContent">
    <requestAndHisSubMenu :routekey="routekey"></requestAndHisSubMenu>
    <backSubMenu :backTitle="backTitle" :lineUnder="false"></backSubMenu>
    <div class="detail">
      <div class="left">
          <cutImage ref="cutImageCanvas"></cutImage>
      </div>
      <div class="right">
        <div class="infor_form_detail">
          <div>
            <!-- Time -->
            <label>Time</label>
            <input type="text" :value="$util.timestampToDay(record.created_at)" readonly>
          </div>
          <!-- <div>
            <label>Event</label>
            <input type="text" :value="detailData.name" readonly>
          </div> -->
          <div class="hasDot">
            <!-- Situation -->
            <label>Situation</label>
            <input type="text" :value="record.status" readonly>
            <span :class="record.status=='success'?'greenDot':'redDot'" id="statusDot">
              <i class="iconfont amd-dot"></i>
            </span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import requestAndHisSubMenu from "@/components/requestAndHisSubMenu";
import backSubMenu from "@/components/backSubMenu";
import cutImage from "@/components/imageCanvas/cutImage";
import { mapGetters } from "vuex";
export default {
  data() {
    return {
      routekey: ["fingerprintRequest", "fingerprintHistory;fingerprintHistoryDetail"],
      loading: false,
      backTitle: "Details",
      resultData: "",
      record:"",
      fileObjSrc:"",
    };
  },
  components: {
    requestAndHisSubMenu,
    cutImage,
    backSubMenu,
  },
  computed: {
    ...mapGetters(["apiHistoryList","imgIp"])
  },
  methods: {
    //定位解析
    getPosition(backData) {
      let position = [];
      console.log("getPosition", backData);
      if (backData.length > 0) {
        for (let i = 0; i < backData.length; i++) {
          let itemData = backData[i].position ? backData[i].position : [];
          console.log("itemData", itemData);
          position[i] = [
            parseInt(itemData[0]),
            parseInt(itemData[1]),
            parseInt(itemData[2]) - parseInt(itemData[0]),
            parseInt(itemData[7]) - parseInt(itemData[1])
          ];
          console.log("position[i]:" + i + ",", position[i]);
        }
      } else {
        position = [[0, 0, 0, 0]];
      }
      console.log("position", position);
      // this.$refs.circleImage.showCircleImage(this.fileObj.fileImage, position);
      this.$refs.cutImageCanvas.initCanvas2(this.fileObjSrc, position);

    },
    getDetailData() {
      let params = this.$route.params;
      console.log(params);
      this.record=params.record;
      this.resultData = params.record.response.data;
      this.fileObjSrc = this.imgIp + params.record.request.image_file;
      console.log(this.resultData);
      this.getPosition(this.resultData);
    }
  },
  mounted() {
    if(this.$route.params.record){
      this.getDetailData();
    }else{
      this.$router.go(-1);
    }
  },
  created() {
  }
};
</script>

<style lang="less">
@import "../../../css/page.less";
</style>



